<template>
  <div class="container">
    <h2>註冊表單</h2>
    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label for="username">帳號 *</label>
        <input type="text" id="username" v-model="form.username" required>
      </div>
      <div class="form-group">
        <label for="password">密碼 *</label>
        <input type="password" id="password" v-model="form.password" required>
      </div>
      <div class="form-group">
        <label for="confirmPassword">確認密碼 *</label>
        <input type="password" id="confirmPassword" v-model="form.confirmPassword" required>
      </div>
      <div class="form-group">
        <label for="realName">真實姓名 *</label>
        <input type="text" id="realName" v-model="form.realName" required>
      </div>
      <div class="form-group">
        <label for="email">電子郵件</label>
        <input type="email" id="email" v-model="form.email">
      </div>
      <div class="form-group">
        <label for="phone">電話號碼</label>
        <input type="tel" id="phone" v-model="form.phone">
      </div>
      <div class="form-group">
        <label for="mood">心情留言</label>
        <textarea id="mood" v-model="form.mood"></textarea>
      </div>
      <input type="submit" :value="register">
    </form>
  </div>
</template>


<script>
export default {
  data() {
    return {
      form: {
        username: '',
        password: '',
        confirmPassword: '',
        realName: '',
        email: '',
        phone: '',
        mood: '',
        status: 'normal',
        
      },
      register: '註冊',
    }
  },
  methods: {
    submitForm() {
      this.register='處理中，請稍後';
      const scriptURL='https://script.google.com/macros/s/AKfycbxYt8auCq1IBMxVRDsGs9NQnzlyAPw7q3w1_gnd7N8ix0MBb18YKaXFWg5wGvDaZd2l/exec';
      const form = new FormData();
      form.append('time', new Date().toLocaleString()); 
      form.append('username', this.form.username);
      form.append('password', this.form.password);
      form.append('status',this.form.status);
      form.append('realName', this.form.realName);
      form.append('email', this.form.email);
      form.append('phone',this.form.phone);
      form.append('mood',this.form.mood);
      
      fetch(scriptURL, { method: 'POST', body: form })
      // eslint-disable-next-line no-unused-vars
        .then(response => {
          alert("註冊成功！！！");
          this.$router.push('/');
        })
        .catch(error => console.error('Error!', error.message));
    }
  }
}
</script>

